import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import ygcComment from '@/components/ygc-comment/ygc-comment.vue';
export default{
	components:{
		uniLoadMore,
		ygcComment
	},
	data(){
		return{
			id:'',
			active:1,
			tabActive:0,
			sh:'',
			sh2:'',
			dataList:[],
			commentList:[],
			data:'',
			like:'',
			comment:'',
			repost:'',
			page:1,
			pageSize:5,
			total:0,
			more:''
		}
	},
	onLoad(e){
		this.id=e.id
		this.like=e.like
		this.comment=e.comment
		this.repost=e.repost
	},
	onPageScroll(e){
		console.log(e)
	},
	computed: {
	     noMore () {
	       return this.commentList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
	   	this.getDetail()
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
				console.log(res)
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods:{
		toggleTab(t){
			this.tabActive=t
		},
		getDetail(){
			var that=this
			var params={
			  id:this.id
			}
			  var url='&r=api.college.hotarticle.detail&id='+this.id
			  this.$apiPost(url,params).then((res) =>{
				  that.data=res.data
				  that.$nextTick(function(){
				  	that.calcArticleHeight()
				  })
				  that.getList()
			  })
		},
		getList(p){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.college.hotarticle.comment&page='+this.page+'&pagesize='+this.pageSize+'&collegeid='+this.id
			  this.$apiPost(url).then((res) =>{
				  that.total=res.total
				  that.commentList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		calcArticleHeight(){
			uni.getSystemInfo({
			  	success: (resu) => {
			  	const query = uni.createSelectorQuery()
			  	query.select('#article').boundingClientRect()
			  	query.exec(function(res) {
						console.log(res)
			  		})
			  	}
			 })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
		pubComment(){
			this.$refs.ygcComment.maskState=0
			this.$refs.ygcComment.content=''
		},
		openComment(){
			this.$refs.ygcComment.maskState=1
		},
		
	}
}
